@import "https://fonts.googleapis.com/css2?family=Montserrat:wght@400;500;700;900&family=Open+Sans&display=swap";
:root {
  --fw-light: 300;
  --fw-normal: 400;
  --fw-semi: 500;
  --fw-bold: 700;
  --fw-black: 900;
  --fs-h1: 1.65rem;
  --fs-h2: 1.3rem;
  --fs-h3: 1.15rem;
  --fs-body: 1rem;
  --fs-small: .85rem;
  --fs-icon: 1.5rem;
  --clr-primary: #eb7f51;
  --clr-dark: #1f2233;
  --clr-dark-light: #24283d;
  --clr-white: #fff;
  --clr-grey: #ceced2;
}

@media screen and (width >= 43.75em) {
  :root {
    --fs-h1: 2.5rem;
    --fs-h2: 1.5rem;
    --fs-h3: 1.35rem;
    --fs-small: .9rem;
    --fs-icon: 1.8rem;
  }
}

html, body, div, span, applet, object, iframe, h1, h2, h3, h4, h5, h6, p, blockquote, pre, a, abbr, acronym, address, big, cite, code, del, dfn, em, img, ins, kbd, q, s, samp, small, strike, strong, sub, sup, tt, var, b, u, i, center, dl, dt, dd, ol, ul, li, fieldset, form, label, legend, table, caption, tbody, tfoot, thead, tr, th, td, article, aside, canvas, details, embed, figure, figcaption, footer, header, hgroup, menu, nav, output, ruby, section, summary, time, mark, audio, video {
  font-size: 100%;
  font: inherit;
  vertical-align: baseline;
  border: 0;
  margin: 0;
  padding: 0;
  text-decoration: none;
}

article, aside, details, figcaption, figure, footer, header, hgroup, menu, nav, section {
  display: block;
}

body {
  line-height: 1;
}

ol, ul {
  list-style: none;
}

blockquote, q {
  quotes: none;
}

blockquote:before, blockquote:after, q:before, q:after {
  content: "";
  content: none;
}

table {
  border-collapse: collapse;
  border-spacing: 0;
}

h1, h2, h3 {
  line-height: 1;
}

img {
  object-fit: cover;
  width: 100%;
  height: 100%;
}

a {
  outline: none;
}

button {
  border: none;
  outline: none;
}

*, :before, :after {
  box-sizing: border-box;
}

html {
  scroll-behavior: smooth;
}

body, html {
  font-family: Montserrat, sans-serif;
  line-height: 1.6;
  font-size: var(--fs-body);
  color: var(--clr-grey);
  width: 100%;
}

body {
  height: 100%;
}

main {
  overflow-x: hidden;
}

p {
  font-family: Open Sans, sans-serif;
  line-height: 1.6;
}

nav {
  all: unset;
}

input {
  box-sizing: border-box !important;
}

.section {
  padding: 5em 1em;
}

.mx-width {
  width: 100%;
  max-width: 1200px;
  margin: auto;
}

.icon {
  color: var(--clr-primary);
  font-size: var(--fs-icon);
  cursor: pointer;
  transition: all .4s cubic-bezier(.22, .43, .35, 1.5);
}

.icon:hover, .icon:focus {
  color: var(--clr-grey);
}

.btn {
  background-color: var(--clr-primary);
  color: var(--clr-white);
  font-size: .6rem;
  font-weight: var(--fw-bold);
  letter-spacing: 3px;
  text-align: center;
  text-transform: uppercase;
  cursor: pointer;
  border: 2px solid #0000;
  justify-content: center;
  align-items: center;
  padding: .8em 4.1em;
  line-height: 1;
  transition: all .4s cubic-bezier(.22, .43, .35, 1.5);
  display: inline-flex;
}

.btn:hover, .btn:focus {
  border: 2px solid var(--clr-primary);
  color: var(--clr-primary);
  background-color: #0000;
  transform: scale(.96)translateY(4px);
}

.btn--outline {
  border: 2px solid var(--clr-primary);
  color: var(--clr-white);
  background-color: #0000;
}

.btn--outline:hover, .btn--outline:focus {
  background-color: var(--clr-primary);
  color: var(--clr-white);
}

.title {
  margin-bottom: 1em;
  font-weight: bold;
}

.title .top {
  text-transform: capitalize;
  color: var(--clr-primary);
  font-size: var(--fs-h3);
  margin-bottom: .3em;
}

.title .bottom {
  text-transform: uppercase;
  font-size: var(--fs-h2);
  letter-spacing: .7px;
  position: relative;
}

.title .bottom:before {
  content: "";
  background: linear-gradient(-65deg, var(--clr-primary), #c66034, var(--clr-primary), #c25729);
  background-size: 400% 400%;
  width: 3em;
  height: 3.3px;
  animation: 15s infinite gradientBG;
  position: absolute;
  bottom: -6px;
  left: 0;
}

.para {
  color: var(--clr-grey);
  margin: 1.5em 0;
  line-height: 1.7;
}

.email--box {
  font-weight: var(--fw-semi);
  cursor: pointer;
  align-items: center;
  font-size: .9rem;
  transition: all .4s cubic-bezier(.22, .43, .35, 1.5);
  display: flex;
}

.email--box i {
  margin-right: .8em;
}

.email--box:hover, .email--box:focus {
  color: var(--clr-primary);
}

.addBox {
  padding: 13em 1em 15em;
}

.addBox:before, .addBox:after {
  content: "";
  background: url("box.1922defa.png") center / cover;
  width: 100%;
  height: 100px;
  position: absolute;
  left: 0;
  right: 0;
}

.addBox:before {
  top: -3px;
  transform: scaleY(-1);
}

.addBox:after {
  bottom: -3px;
}

.animated-gradient {
  background: linear-gradient(-65deg, var(--clr-primary), #c66034, var(--clr-primary), #c25729);
  background-size: 400% 400%;
  animation: 15s infinite gradientBG;
}

@keyframes gradientBG {
  0% {
    background-position: 0%;
  }

  50% {
    background-position: 100%;
  }

  100% {
    background-position: 0%;
  }
}

@keyframes pulse {
  from {
    transform: scale(1)translate(-50%, -50%)rotate(0);
  }

  to {
    transform: scale(.8)translate(-50%, -50%)rotate(-360deg);
  }
}

.load-screen {
  z-index: 2000;
  pointer-events: none;
  background: linear-gradient(-20deg, var(--clr-dark-light), var(--clr-dark), var(--clr-dark-light), var(--clr-dark));
  background-size: 400% 400%;
  flex-wrap: wrap;
  justify-content: center;
  align-items: center;
  width: 100%;
  height: 100vh;
  min-height: 100vh;
  font-size: 10vh;
  animation: 5s ease-in-out infinite alternate gradientBG;
  display: flex;
  position: fixed;
  inset: 0;
  overflow: hidden;
}

.load-screen span {
  opacity: 0;
  font-weight: var(--fw-black);
  animation: .75s ease-in-out infinite alternate loadPulse;
}

.load-screen span:nth-child(2) {
  animation-delay: .2s;
}

.load-screen span:last-child {
  animation-delay: .3s;
}

@-webkit-keyframes loadPulse {
  to {
    opacity: 0;
    transform: scale(.8);
  }
}

@keyframes loadPulse {
  to {
    opacity: .5;
    transform: scale(.8);
  }
}

@media only screen and (width >= 62em) {
  .fallow--curser {
    border: 2px solid var(--clr-white);
    pointer-events: none;
    z-index: 1000;
    border-radius: 50%;
    width: 3rem;
    height: 3rem;
    position: absolute;
    transform: translate(-50%, -50%);
  }

  .cursor-animate {
    border-color: var(--clr-primary);
    border-top-color: #eb7f510d;
    border-bottom-color: #eb7f510d;
    width: 2rem;
    height: 2rem;
    animation: .7s linear infinite alternate pulse;
  }
}

.portfolio {
  object-fit: cover;
  background-color: #0000;
  background-image: linear-gradient(#24283dfa, #24283dfa), url("blackBg2.32c8465e.jpg");
  background-position: center;
  background-repeat: no-repeat;
  background-size: cover;
  background-attachment: scroll, scroll;
  background-origin: padding-box, padding-box;
  background-clip: border-box, border-box;
  padding: 10em 1em;
  position: relative;
}

.portfolio:before, .portfolio:after {
  content: "";
  clip-path: polygon(0 0, 100% 0, 100% 30%, 0 100%);
  background-color: var(--clr-primary);
  width: 100%;
  height: 7.5em;
  position: absolute;
  left: 0;
  right: 0;
}

.portfolio:before {
  top: 0;
}

.portfolio:after {
  bottom: 0;
  transform: scale(-1);
}

.portfolio--content {
  max-width: 1150px;
  margin: auto;
}

.portfolio--content__card {
  margin-bottom: 2.5em;
}

.carousel {
  overflow: visible;
}

.carousel .carousel-item {
  place-content: center;
  display: grid;
}

.carousel .carousel-item img {
  height: auto;
}

.screen-container {
  --screen-w: 22em;
  --screen-h: 201px;
  --page-w: 85%;
  --page-m: 1em auto .6em;
  width: var(--screen-w);
  height: var(--screen-h);
  display: grid;
  overflow: hidden;
}

.screen-container .screen, .screen-container .page {
  grid-area: 1 / -1;
}

.screen-container .page {
  margin: var(--page-m);
  width: var(--page-w);
  z-index: -1;
  height: auto;
  overflow: hidden;
}

.screen-container .page img {
  background-color: #000;
}

.carousel-item.active .page img {
  animation: 20s linear 1s infinite alternate scrollPage;
}

.carousel-item.active .mobile .page img {
  animation: 30s linear 1s infinite alternate scrollPage;
}

.portfolio--content__card:nth-of-type(4) .carousel-item.active .page img {
  animation: 15s linear 1s infinite alternate scrollPage;
}

.portfolio--content__card:nth-of-type(4) .carousel-item.active .laptop .page img {
  animation: 5s linear 1s infinite alternate scrollPageKeep;
}

.laptop {
  --page-w: 75%;
}

.tablet {
  --screen-w: 13em;
  --screen-h: 270px;
  --page-m: 1.5em auto .6em;
}

.mobile {
  --screen-w: 9em;
  --screen-h: 286px;
  --page-m: .72em auto .6em;
}

.portfolio--content__card--info {
  grid-template-columns: 1fr auto;
  display: grid;
}

.portfolio--content__card--info .info--title {
  color: var(--clr-primary);
  font-weight: var(--fw-bold);
  font-size: var(--fs-h3);
  letter-spacing: .7px;
  text-transform: uppercase;
  grid-column: 1 / 2;
  position: relative;
}

.portfolio--content__card--info .info--title:before {
  content: "";
  background-color: var(--clr-white);
  width: 4em;
  height: 3.3px;
  position: absolute;
  bottom: 1px;
  left: 0;
}

.portfolio--content__card--info .github {
  grid-column: 2 / 3;
  justify-self: end;
  margin-right: 2em;
}

.portfolio--content__card--info .para, .portfolio--content__card--info .keys, .portfolio--content__card--info .btn {
  grid-column: 1 / -1;
}

.portfolio--content__card--info .keys {
  margin-bottom: 2.5em;
}

.portfolio--content__card--info .keys span {
  color: var(--clr-grey);
  font-size: .9rem;
  font-weight: var(--fw-semi);
  text-transform: capitalize;
  border-radius: 5px;
  margin: .3em .5em .3em 0;
  padding: .35em .4em;
  display: inline-block;
}

.portfolio--content__card--info .keys span:first-child {
  margin-left: 0;
}

.portfolio--content__card--info .keys span:last-child {
  margin-right: 0;
}

.portfolio--content__card--info .btn {
  justify-self: center;
}

@media screen and (width >= 43.75em) {
  .portfolio {
    padding: 13em 1em;
  }

  .portfolio--content__card {
    grid-gap: 1em;
    grid-template-columns: repeat(2, 1fr);
    align-items: center;
    gap: 1em;
    display: grid;
  }

  .portfolio--content__card:nth-child(2n) .portfolio--content__card--screens {
    grid-area: 1 / 2 / 1 / 3;
  }

  .portfolio--content__card:nth-child(2n) .portfolio--content__card--info {
    justify-self: start;
  }

  .portfolio--content__card--info {
    grid-template-columns: auto 1fr;
    place-self: center end;
    align-items: center;
    max-width: 32em;
  }

  .portfolio--content__card--info .info--title {
    grid-column: 1 / -1;
  }

  .portfolio--content__card--info .info--title:before {
    bottom: -6px;
  }

  .portfolio--content__card--info .github {
    grid-area: 4 / 2 / 4 / 3;
    justify-self: start;
    margin-left: 2em;
  }

  .portfolio--content__card--info .btn {
    grid-column: 1 / 2;
    justify-self: start;
  }

  .portfolio--content__card--info .keys {
    margin-bottom: 2em;
  }
}

@media only screen and (width >= 62em) {
  .portfolio--content__card {
    grid-gap: 0em;
    gap: 0;
  }
}

@keyframes scrollPage {
  from {
    transform: translateY(0);
  }

  to {
    transform: translateY(calc(19em - 100%));
  }
}

@keyframes scrollPageKeep {
  from {
    transform: translateY(0);
  }

  to {
    transform: translateY(calc(10em - 100%));
  }
}

.hero {
  text-align: center;
  background: linear-gradient(#1f2233e6, #31385be6);
  justify-content: center;
  align-items: center;
  min-height: 98vh;
  display: flex;
  position: relative;
  overflow: hidden;
}

.hero:before {
  content: "";
  background-color: #0003;
  width: 100%;
  height: 100%;
  position: absolute;
  top: 0;
  left: 0;
  right: 0;
}

.video--bg {
  object-fit: cover;
  z-index: -1;
  opacity: 1;
  background-color: #000;
  min-width: 100%;
  height: 100%;
  min-height: 100%;
  position: absolute;
  top: 50%;
  left: 50%;
  transform: translate(-50%, -50%);
}

.hero--content {
  z-index: 1;
  padding: 0 2em;
}

.hero--content__avatar {
  background-color: var(--clr-dark-light);
  border: 6px solid #ceced214;
  border-radius: 50%;
  width: 160px;
  height: 160px;
  margin: auto auto .5em;
  overflow: hidden;
  box-shadow: 1px 10px 15px #0000004d;
}

.hero--content__avatar .avatar--img {
  background: url("avatar.ab9f5cf9.png") 0 12% / cover;
  border-radius: 50%;
  width: 100%;
  height: 100%;
}

.hero--content__title {
  font-size: 7vw;
  font-weight: var(--fw-black);
  color: var(--clr-primary);
  letter-spacing: 2.5px;
  text-transform: uppercase;
  margin-bottom: .3em;
}

.hero--content__subtitle {
  font-size: .9rem;
  font-weight: var(--fw-bold);
}

.hero--content__links {
  margin: 1.4em 0 2em;
}

.hero--content__links a {
  margin: 0 .5em;
}

.hero--content .btn--outline {
  color: var(--clr-primary);
  margin: auto;
}

.hero--content .btn--outline:hover, .hero--content .btn--outline:focus {
  color: var(--clr-white);
  background-color: #0000;
}

@media screen and (width >= 43.75em) {
  .hero {
    min-height: 95vh;
  }

  .hero--content__avatar {
    border: 7px solid #ceced214;
    width: 200px;
    height: 200px;
    margin-bottom: 1.3em;
  }

  .hero--content__subtitle {
    font-size: .95rem;
  }

  .hero--content__title {
    font-size: var(--fs-h1);
  }
}

@media only screen and (width >= 62em) {
  .hero--content__avatar {
    width: 220px;
    height: 220px;
    margin-bottom: 2em;
  }

  .hero--content__subtitle {
    font-size: 1rem;
  }

  .hero--content__links {
    margin-top: 2em;
  }

  .hero--content .btn--outline {
    display: none;
  }
}

@media screen and (orientation: landscape) and (device-width <= 56.25em) {
  .hero--content {
    padding: 8em 2em;
  }
}

.header {
  position: absolute;
  left: 0;
  right: 0;
}

.nav {
  z-index: 10;
  background-color: #0000;
  align-items: center;
  padding: 1em;
  transition: all .27s ease-in;
  display: flex;
  position: relative;
}

.nav--logo {
  width: 40px;
  margin-left: 1em;
  margin-right: auto;
}

.nav--toggle {
  cursor: pointer;
  background-color: #0000;
  padding: .5em;
  position: relative;
}

.nav--toggle:focus {
  background: none;
}

.nav--toggle:hover .hamburger, .nav--toggle:hover .hamburger:before, .nav--toggle:hover .hamburger:after, .nav--toggle:focus .hamburger, .nav--toggle:focus .hamburger:before, .nav--toggle:focus .hamburger:after {
  background: var(--clr-primary);
}

.nav--portfolio {
  color: var(--clr-white);
  background-color: var(--clr-primary);
  font-size: .7rem;
  font-weight: var(--fw-bold);
  border: 2px solid #0000;
  border-radius: 5px;
  justify-content: space-evenly;
  align-items: center;
  width: -moz-fit-content;
  width: fit-content;
  margin-right: 5em;
  padding: .2em 1.8em;
  transition: all .4s cubic-bezier(.22, .43, .35, 1.5);
  display: flex;
}

.nav--portfolio:hover, .nav--portfolio:focus {
  opacity: .8;
  border-color: var(--clr-primary);
  background-color: #0000;
}

.nav--portfolio i {
  margin-left: .5em;
}

.nav .btn {
  color: var(--clr-primary);
  padding: 0 2.5em;
  line-height: 1;
  display: none;
}

.nav .btn:hover, .nav .btn:focus {
  color: var(--clr-white);
  background-color: #0000;
}

.nav--container {
  background-color: var(--clr-dark);
  flex-direction: column;
  align-items: flex-start;
  width: 100%;
  height: 0;
  transition: all .27s ease-in;
  display: flex;
  position: absolute;
  top: 100%;
  left: 0;
  right: 0;
  overflow: hidden;
  box-shadow: 0 0 15px #0000001f;
}

.nav--container .nav--links {
  opacity: 0;
  color: var(--clr-white);
  border-bottom: 1px solid #ceced233;
  width: 100%;
  height: 100%;
  padding: .5em 0 .5em 2em;
  transition: all .4s cubic-bezier(.22, .43, .35, 1.5);
  display: block;
  position: relative;
  transform: scale(0);
}

.nav--container .nav--links:last-child {
  border-bottom: none;
}

.nav--container .nav--links:hover, .nav--container .nav--links:focus {
  color: var(--clr-primary);
}

.hamburger, .hamburger:before, .hamburger:after {
  background: var(--clr-white);
  height: 3px;
  transition: all .27s ease-in;
}

.hamburger {
  cursor: pointer;
  width: 2em;
  display: block;
  position: relative;
}

.hamburger:before, .hamburger:after {
  content: "";
  z-index: -1;
  position: absolute;
  left: 0;
}

.hamburger:before {
  width: 1em;
  bottom: 6px;
}

.hamburger:after {
  width: 1em;
  margin-left: 1em;
  top: 6px;
}

.nav-open .nav {
  background-color: var(--clr-dark);
  box-shadow: 0 0 15px #0000001f;
}

.nav-open .hamburger, .nav-open .hamburger:before, .nav-open .hamburger:after {
  background: var(--clr-primary);
}

.nav-open .nav--container {
  height: 13.17em;
}

.nav-open .nav--container .nav--links {
  opacity: 1;
  transform: scale(1);
}

.nav-open .hamburger {
  transform: rotate(45deg);
}

.nav-open .hamburger:before {
  transform: rotate(90deg)translate(-1px, -6px);
}

.nav-open .hamburger:after {
  transform: rotate(-90deg)translate(-1px, -9px);
}

.scrollNav {
  z-index: 50;
  background-color: var(--clr-dark);
  animation: .3s ease-in-out forwards easeIN;
  position: fixed;
  box-shadow: 0 0 15px #0000001f;
}

@keyframes easeIN {
  from {
    opacity: 0;
  }

  to {
    opacity: 1;
  }
}

@media screen and (width >= 43.75em) {
  .nav--logo {
    width: 47px;
  }

  .nav--portfolio {
    margin-right: 7em;
    font-size: .75rem;
  }

  .nav--container .nav--links {
    padding: .5em 0 .5em 2em;
  }
}

@media only screen and (width >= 62em) {
  .nav--toggle, .nav--portfolio {
    display: none;
  }

  .nav .btn {
    display: inline-flex;
  }

  .nav--container {
    text-align: center;
    box-shadow: none;
    background-color: #0000;
    flex-direction: row;
    justify-content: space-between;
    width: 550px;
    height: auto;
    margin-right: 3em;
    display: flex;
    position: relative;
  }

  .nav--container .nav--links {
    opacity: 1;
    color: var(--clr-white);
    font-weight: var(--fw-bold);
    border-bottom: 2px solid #0000;
    width: auto;
    height: auto;
    padding: 0 .2em;
    display: block;
    transform: scale(1);
  }

  .nav--container .nav--links:before {
    content: "";
    background-color: var(--clr-primary);
    transform-origin: center;
    height: 2px;
    transition: all .4s cubic-bezier(.22, .43, .35, 1.5);
    position: absolute;
    bottom: 0;
    left: 0;
    right: 0;
    transform: scale(0);
  }

  .nav--container .nav--links:hover:before, .nav--container .nav--links:focus:before {
    transform: scale(1);
  }

  .nav--container .nav--links:hover, .nav--container .nav--links:focus, .nav--container .nav--links.active {
    color: var(--clr-white);
  }

  .nav--container .nav--links.active:before, .nav--container .nav--links.active:before {
    transform: scale(1);
  }
}

.contact {
